// rem defined not useable here so px/16rem 1rem=1em = 16px

// Desktop 1200 px(Smaller desktop)
@media (max-width: 75em) {
  .main-container {
    width: 100rem;
    height: 55rem;
  }

  .headline {
    font-size: 1.4rem;
  }

  .task-topic-textbox h2,
  .task-topic-textbox span {
    font-size: 2.2rem;
  }

  .task-topic-textbox span {
    height: 3.4rem;
  }
}

// Tablets 992px(Tablet landscape)
@media (max-width: 62em) {
  .main-container {
    width: 80rem;
  }

  .headline {
    font-size: 1.2rem;
  }

  .task-topic-textbox h2,
  .task-topic-textbox span {
    font-size: 2rem;
  }

  .task-topic-textbox span {
    height: 3rem;
  }

  .task-column {
    margin: 1rem;
  }

  .first-column,
  .task-list {
    font-size: 1.2rem;
  }

  .second-column {
    margin: 0;
    padding: 0;

    margin-top: 1rem;
  }

  .second-headline,
  .add-task-box {
    margin: 1rem;
  }

  .task-list {
    padding: 0.5rem;
  }

  .task-list input {
    margin-top: 0.3rem;
  }

  .describe-main {
    padding: 0.5rem;
  }
}

//Tablets 768px(tablets-portrait)
@media (max-width: 48rem) {
  .main-container {
    grid-template-columns: 5rem 1fr 1fr;

    min-width: 30rem;
    width: 55rem;
    height: 60rem;

    gap: 0;
  }

  .sidebar-btn svg {
    width: 100%;
  }
}
